body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0; }

.app {
  min-height: 100vh;
  background-color: #ffe62c;
  font-family: "Didact Gothic", sans-serif;
  font-size: 1em;
  text-align: center;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.app__filter-name {
  width: 30%;
  border: 2px solid black;
  background-color: #5df5cf; }

.triangle {
  position: fixed; }

.t-left {
  left: 0;
  border-right: 30px solid transparent;
  border-top: 30px solid black;
  border-left: 30px solid black;
  border-bottom: 30px solid transparent; }

.t-right {
  right: 0;
  border-right: 30px solid black;
  border-top: 30px solid black;
  border-left: 30px solid transparent;
  border-bottom: 30px solid transparent; }

.app__title {
  padding: 5%; }

.app__list {
  list-style: none;
  padding: 50px;
  margin: 0px 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }
  @media all and (min-width: 768px) {
    .app__list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr; } }

.app__list-item {
  list-style: none; }

.pokemon__types {
  list-style: none; }

.pokemon__wrapper {
  border: 2px solid black;
  z-index: 4;
  width: 220px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 10px; }

.pokemon__wrapper-grey {
  background-color: silver;
  position: relative;
  -webkit-flex-grow: 2;
          flex-grow: 2;
  min-height: 140px; }

.pokemon__image {
  display: block;
  margin: 10% 30%; }

.pokemon__id {
  text-align: left;
  background-color: #dbdada;
  color: #bb9c74;
  padding: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 12px; }

.pokemon__wrapper-info {
  background-color: white;
  padding: 5px;
  text-align: left; }

.pokemon__name {
  text-transform: capitalize;
  font-size: 18px;
  font-weight: 400;
  margin: 5px 0; }

.pokemon__types {
  list-style: none;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row; }

.pokemon__types-item {
  border: solid 1px silver;
  border-radius: 25%;
  padding: 3px;
  margin: 2px;
  font-size: 12px;
  text-transform: uppercase; }

.circle {
  position: fixed;
  background-color: red;
  width: 45px;
  height: 45px; }

.c-left {
  left: 0;
  bottom: 0;
  border-radius: 0 100% 0 0; }

.c-right {
  right: 0;
  bottom: 0;
  border-radius: 100% 0 0 0; }

